import React, { useEffect, useState } from "react";

function getDados(setSucess){
  fetch('http://localhost:8080/api/users?token='+localStorage.getItem("TOKEN_KEY")+"&authenticateToken=JG2OI@JWG2131*j)(", {
      method: "GET"
  }).then(res => res.json())
  .then(user => {
      if(user.sucess) return setSucess(true);
  });
}

 function Login({handleSubmit, validateForm, setPassword, setEmail, email, password}) {

  return (
    <>
                                    <form className="user" onSubmit={handleSubmit}>
                    
                
                    <div className="form-group">
                        <label htmlFor="email">Email</label>
                        <input type="email" onChange={(e) => setEmail(e.target.value)} className="form-control form-control-user" id="email" name="email" aria-describedby="emailHelp" placeholder="Insira Seu E-Mail" />
                    </div>
                    <div className="form-group">
                        <label htmlFor="senha">Senha</label>
                        <input type="password" onChange={(e) => setPassword(e.target.value)} className="form-control form-control-user" id="senha" name="password" placeholder="Insira Sua Senha" />
                    </div>
                    <center>
                        <button className="btn btn-login" type="submit" disabled={!validateForm()}>Entrar</button>
                    </center>
                </form>
    </>
    );
}
export default Login