import React from 'react';
import { Link } from 'react-router-dom';
import Image from '../Navbar/Logo.png';

function Footer() {
    
    return (
        <>
        <footer className="footer">
    <div className="row">
        <div className="col-md-8">
            <div className="logo">
                <img src={Image} alt="Logo" />
            </div>
        </div>
        <div className="col-md-4">
            Obrigado por ter chego até aqui, quer solicitar um orçamento? entre em contato comigo pelo discord! tchau tchau.
            <div className="buttons">
                <button className="btn btn-discord" type="button">
                    <Link to="/discord">
                    <i className="mdi mdi-discord"></i>
                    </Link>
                </button>
            </div>
        </div>
    </div>
    <center>
        <div className="developed">
            <label>
                <i className="mdi mdi-code-tags"></i> Developed by KoddyDev
            </label>
            <br />
        </div>
    </center>   
</footer>
        </>
    )
}

export default Footer;