import React from 'react';
import { Link } from 'react-router-dom';
import Image from './Logo.png'


function Header() {
    
    return (
        <>
        <section className="header" area="home" id="home">
<nav className="navbar navbar-expand-lg">
    <div className="brand">
        <div className="logo">
            <Link to="/">
                <img src={Image} alt="Logo"/>
            </Link>
        </div>
    </div>
    <div className="switcher">
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#buttons" aria-controls="buttons" aria-expanded="false" aria-label="Switcher Responsivo">
            <i className="mdi mdi-view-day"></i>
        </button>
    </div>
    <div className="container">
        <div className="collapse navbar-collapse" id="buttons">
            <ul className="navbar-nav buttons">
                <li className="nav-item">
                        <Link to="/" className="nav-link">Página Inicial</Link>
                </li>
                <li className="nav-item">
                    <div className="dropdown">
                        <Link to="/encomendas" className="nav-link">
                            Projetos
                        </Link>
                    </div>
                </li>
                <li className="nav-item">
                    <div className="dropdown">
                        <Link to="/financeiro" className="nav-link">
                            Financeiro
                        </Link>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</nav>
</section>
        </>
    )
}

export default Header;