import React, { useEffect, useState } from 'react';
import { Form } from 'react-bootstrap';
import { Redirect } from 'react-router-dom';
import HeaderC from './Navbar';

function Mensagens({title, message, classe}) {
    return (
        <>
        {title && message && classe &&
        
            <div className="titulos">
            <h5 className={classe}>{ title }</h5>
        <p className="desc">{message}</p>
        </div>
        }
        </>
        
    
    )
}

function getDados(setUser, setSucess){
    fetch('http://localhost:8080/api/users?token='+localStorage.getItem("TOKEN_KEY")+"&authenticateToken=JG2OI@JWG2131*j)(", {
        method: "GET"
    }).then(res => res.json())
    .then(user => {
        if(!user.sucess) {
setSucess(false)
                       
        } else {
            setUser(user)
        }
    }).catch(err => {
        if(err = "TypeError: Failed to fetch")  {
                return (
                  <>
                  <center>
                  <div class="titulos">
                          <h5 class="titulor">ERRO!</h5>
                      <p class="desc">Estamos com problemas entre nosso servidor e a nossa API, nosso técnico já foi alertado e estaremos trabalhando para resolver o mais rapido possivel. </p>
                      </div>
                  </center>
                      </>
                )
                }
});
}

function Account(){
    let [senha, setSenha] = useState("")
    let [email, setEmail] = useState("")
    let [message, setMessage] = useState("")
    let [title, setTitle] = useState("")
    let [classe, setClasse] = useState("")
    let [usuario, setUser] = useState({})
    let [sucess, setSucess] = useState(true)
    useEffect(() => {
        getDados(setUser, setSucess)

    }, [])
    

    function handleSubmitEmail(event){
        event.preventDefault();

        fetch('http://localhost:8080/api/users/update/email?code='+localStorage.getItem("TOKEN_KEY")+"&email="+email+"&senha="+senha+"&authenticateToken=JG2OI@JWG2131*j)(", {
        method: "GET"
    }).then(res => res.json())
    .then(body => {
        if(!body.sucess) {
            setClasse("titulor")
            setTitle("ERRO!")
            setMessage(body.motivo)
        } else {
            setClasse("titulov")
            setTitle("Sucesso!")
            setMessage(body.motivo)
        }
    }).catch(err => {
        if(err = "TypeError: Failed to fetch")  {
                return (
                  <>
                  <center>
                  <div class="titulos">
                          <h5 class="titulor">ERRO!</h5>
                      <p class="desc">Estamos com problemas entre nosso servidor e a nossa API, nosso técnico já foi alertado e estaremos trabalhando para resolver o mais rapido possivel. </p>
                      </div>
                  </center>
                      </>
                )
                }
});
    }

    function validateForm() {
        return email.length > 0 && senha.length > 0;
      }

if(sucess) {
    return (
        <>
        <section className="header" area="client">
        <HeaderC />
    <center>
        <h3>
            Seja bem vindo a configuração da sua conta
        </h3>
    </center>
    <div className="divisor mt-2">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
            <path fill="var(--dark)" fillOpacity="1" d="M0,64L80,80C160,96,320,128,480,117.3C640,107,800,53,960,37.3C1120,21,1280,43,1360,53.3L1440,64L1440,320L1360,320C1280,320,1120,320,960,320C800,320,640,320,480,320C320,320,160,320,80,320L0,320Z"></path>
        </svg>
    </div>
</section><center>
    <center>
<Mensagens title={title} message={message} classe={classe} />
    </center>
<section className="config">
    <div className="cards" area="config">
        <div className="container">
            <div className="row">
                <div className="col-md-6">
                    <div className="card">
                        <div className="titulos">
                            <h5 className="titulo">
                                Trocar E-Mail
                            </h5>
                            <p className="desc">Troque seu E-Mail</p>
                        </div>
                        <Form onSubmit={handleSubmitEmail} className={'email'}>
                            <Form.Group size="lg" controlId="email">
                            <Form.Label>E-Mail</Form.Label>
                                <Form.Control
                                    type="email"
                                    defaultValue={usuario.email}
                                    onChange={(e) => setEmail(e.target.value)}
                                />
                            </Form.Group>
                            <Form.Group size="lg" controlId="senha">
                            <Form.Label>Senha</Form.Label>
                                <Form.Control
                                    type="password"
                                    defaultValue={senha}
                                    onChange={(e) => setSenha(e.target.value)}
                                />
                            </Form.Group>
                            <center>
                                <button className="btn btn-submit" type="submit" disabled={!validateForm()}>Confirmar</button>
                            </center>
                        </Form>
                    </div>
                </div>
                <div className="col-md-6">
                    <div className="card">
                        <div className="titulos">
                            <h5 className="titulo">
                                Trocar Senha
                            </h5>
                            <p className="desc">Troque sua senha</p>
                        </div>
                        <form method="POST" action="/client/account/senha" className="senha">
                            <div className="form-group">
                                <label htmlFor="password-anti">Senha Antiga:</label>
                                <input type="password" className="form-control" id="password_anti" name="password_anti" placeholder="Insira seu nome novo" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="password-nov">Senha Nova:</label>
                                <input type="password" className="form-control" id="password_nov" name="password_nov" placeholder="Insira sua senha nova" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="senha">Confirmar Senha Nova:</label>
                                <input type="password" className="form-control" id="senha2" name="senha2" placeholder="Repita sua senha nova" />
                            </div>
                            <center>
                                <button className="btn btn-submit" type="submit">Confirmar</button>
                            </center>
                        </form>
                    </div>
                </div>
                <div className="col-md-6">
                    <div className="card">
                        <div className="titulos">
                            <h5 className="titulo">
                                Configurações
                            </h5>
                        
                                    <p className="desc">Suas Informações</p>
                        </div>
                        <form method="POST" action="/client/account/discord" className="discord">
                            <div className="form-group">
                                <label htmlFor="discord">Discord:</label>
                                <input className="form-control" id="discord" name="discord" defaultValue="<%= dados.discord %>" placeholder="Insira seu id do discord" />
                            </div>
                            <center>
                                <button className="btn btn-submit" type="submit">Confirmar</button>
                            </center>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
</center>
</>
    )
} else {
    return <Redirect to="/financeiro/login" />
}
}


export default Account;