import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import HeaderC from './Navbar';

function getDados(setSucess){
    fetch('http://localhost:8080/api/users?token='+localStorage.getItem("TOKEN_KEY")+"&authenticateToken=JG2OI@JWG2131*j)(", {
        method: "GET"
    }).then(res => res.json())
    .then(user => {
        if(!user.sucess) return setSucess(false)
    });
}

function Ranks() {

        const [sucess, setSucess] = useState(true);

        useEffect(() => {
            getDados(setSucess)
        }, [])
if(sucess) {
    return (
        <>
        <section class="header" area="client">
        <HeaderC />
    <center>
        <h3>
            Nosso sistema de Ranks
        </h3>
    </center>
    <div class="divisor mt-2">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
            <path fill="var(--dark)" fill-opacity="1" d="M0,64L80,80C160,96,320,128,480,117.3C640,107,800,53,960,37.3C1120,21,1280,43,1360,53.3L1440,64L1440,320L1360,320C1280,320,1120,320,960,320C800,320,640,320,480,320C320,320,160,320,80,320L0,320Z"></path>
        </svg>
    </div>
</section>
<section class="infos">
    <center>
        <div class="titulos">
            <center>
                <h4 class="titulo">
                    Ranks
                </h4>
                <p class="desc">
                    Informações do sistema de ranks
                </p>
            </center>
        </div>
    </center>
    <div class="cards" area="infos">
        <div class="container">
            <div class="row">
                <div class="col">
                    <div class="card">
                        <span class="titulo">
                            Ranks
                        </span>
                        <div class="cell">
                            <span class="titulo">
                                Quais são os Ranks?
                            </span>
                            <p class="desc">
                                Os Ranks são: 

                                <li>Cliente</li>
                                <li>Cliente Platinum</li>
                                <li>Cliente Gold</li>
                                <li>Cliente Diamond</li>
                                <li>Cliente Saphire</li>
                            </p>
                        </div>
                        <div class="cell">
                            <span class="titulo">
                                Como esse sistema funciona?
                            </span>
                            <p class="desc">
                                Os ranks são ganhos de acordo com a quantidade de projetos comprados conosco e de acordo com seu cargo você tem uma % de desconto no próximo projeto!
                            </p>
                        </div>
                        <div class="cell">
                            <span class="titulo">
                                Quais são beneficios dos ranks?
                            </span>
                            <p class="desc">
                                Os benefícios dos Ranks são: 

                                <li>Cliente Platinum <code>5% de desconto</code></li>
                                <li>Cliente Gold <code>10% de desconto</code></li>
                                <li>Cliente Diamond <code>20% de desconto</code></li>
                                <li>Cliente Saphire <code>30% de desconto</code></li>
                            </p>
                        </div>
                        <div class="cell">
                            <span class="titulo">
                                Quais são os requisitos para cada rank?
                            </span>
                            <p class="desc">
                                Os requisitos são: 

                                <li>Cliente <code>1 Projeto comprado conosco</code></li>
                                <li>Cliente Platinum <code>2 Projetos comprados conosco</code></li>
                                <li>Cliente Gold <code>4 Projetos comprados conosco</code></li>
                                <li>Cliente Diamond <code>6 Projetos comprados conosco</code></li>
                                <li>Cliente Saphire <code>8 Projetos comprados conosco</code></li>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
        </>
    )
} else {
    return (
        <>
        <center>
        <div class="titulos">
                <h5 class="titulor">ERRO!</h5>
            <p class="desc">Você não está logado! Clique <Link to="/financeiro/login">Aqui</Link> para ser redirecionado</p>
            </div>
        </center>
            </>
    )
}
}

export default Ranks;