import React, { useEffect, useState } from "react";
import Logo from '../../Navbar/Logo.png'
import Superagent from "superagent"
import { Redirect } from "react-router-dom";
import Login from './Login'
import Register from './Register';

function Mensagens({title, message, classe}) {
  return (
      <>
      {title && message && classe &&
      
          <div className="titulos">
          <h5 className={classe}>{ title }</h5>
      <p className="desc">{message}</p>
      </div>
      }
      </>
      
  
  )
}

function getDados(setSucess){
  fetch('http://localhost:8080/api/users?token='+localStorage.getItem("TOKEN_KEY")+"&authenticateToken=JG2OI@JWG2131*j)(", {
      method: "GET"
  }).then(res => res.json())
  .then(user => {
      if(user.sucess) return setSucess(false);
  });
}

 function Container() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [password2, setPassword2] = useState("");
  const [IsRegister, setIsRegister] = useState("");
  const [sucess, setSucess] = useState(true)
  let [message, setMessage] = useState("")
  let [title, setTitle] = useState("")
  let [classe, setClasse] = useState("")

  

  useEffect(() => {
      setIsRegister(false)
      if(localStorage.getItem("TOKEN_KEY")) {
        getDados(setSucess)
      }
  }, [])

  useEffect(() => {
    document.body.dataset.area = "login"
   }, [])

  function validateForm() {
    return email.length > 0 && password.length > 0;
  }
  function validateForm2() {
    return email.length > 0 && password.length > 0 && password2.length > 0 && password2 === password && password === password2;
  }

  function handleSubmit(event) {
    event.preventDefault();

    Superagent.get("http://localhost:8080/api/users/login?email="+email+"&senha="+password+"&authenticateToken=JG2OI@JWG2131*j)(")
    .end((err, req) => {

      if(req.body.loggedin)
      {
        localStorage.setItem("TOKEN_KEY", req.body.user.token)
        window.location.pathname = "/financeiro"
      } else {
        setClasse("titulor")
        setTitle("ERRO!")
        setMessage(req.body.motivo)
      }
  })
    }
    function handleSubmit2(event) {
        event.preventDefault();
    
        Superagent.get("http://localhost:8080/api/users/login?email="+email+"&senha="+password+"&authenticateToken=JG2OI@JWG2131*j)(")
        .end((err, req) => {
    
          if(req.body.loggedin)
          {
            localStorage.setItem("TOKEN_KEY", req.body.user.token)
            window.location.pathname = "/financeiro"
          } else {
            setClasse("titulor")
            setTitle("ERRO!")
            setMessage(req.body.motivo)
          }
      })
        }
const setEvent = () => { setIsRegister(!IsRegister); }

if(sucess) {
    
  return (
    <>
    
    <div className="container">
    <div className="row">
        <div className="col-md-6">
            <img className="imagem" src={Logo} alt="Imagem Login - KoddyDev" />
        </div>
            <center>
    </center>
        <div className="col-md-5">
            <button onClick={setEvent}>{!IsRegister ? "Registro" : "Login" }</button>
            <div className="card">
              {title ? <Mensagens title={title} message={message} classe={classe} /> : 
              <div className="titulos">
                  
              <h5 className="titulo">
              {IsRegister ? "Registro" : "Login" } - Área Do Cliente
              </h5>
              <p className="desc">{ IsRegister ? "Registre-se para acessar a area do cliente." : "Realize login para acessar a area do cliente."}</p>
              </div>

              
              }
            {IsRegister ? 
            <Register  validateForm={validateForm2} handleSubmit={handleSubmit2} setEmail={setEmail} setPassword={setPassword} setPassword2={setPassword2} />
        : 
        <Login  validateForm={validateForm} handleSubmit={handleSubmit} />}
            </div>
        </div>
    </div>
</div>
    </>
    );
} else return (
  <>
  <Redirect to="/financeiro" />
  {
    document.body.dataset.area = ""
  }
  </>
)
}
export default Container