import moment from 'moment';
import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import HeaderC from './Navbar';


function getDados(setProjetos, setStatew){
    fetch('http://localhost:8080/api/users/?token='+localStorage.getItem("TOKEN_KEY")+"&authenticateToken=JG2OI@JWG2131*j)(", {
        method: "GET"
    }).then(res => res.json())
    .then(user => {
        if(!user.sucess) return setStatew(false)
        fetch('http://localhost:8080/api/users/projetos?email='+user.email +"&authenticateToken=JG2OI@JWG2131*j)(", {
            method: "GET"
        }).then(res => res.json())
        .then(projetos => {
            setProjetos(projetos)
            setStatew(true)
        });
    });

   
}
function Projetos(){
    let [projetos, setProjetos] = useState({})

    let [state, setStatew] = useState(false);
       useEffect(() => {
        getDados(setProjetos, setStatew)
    },[ ])
    

    if(state) {
   
        return (
            <>
                <section className={"header"} area="client">
                <HeaderC />
            <center>
                <h3>
                    Veja todos os seus projetos
                </h3>
            </center>
            <div className={"divisor mt-2"}>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
                    <path fill="var(--dark)" fillOpacity="1" d="M0,64L80,80C160,96,320,128,480,117.3C640,107,800,53,960,37.3C1120,21,1280,43,1360,53.3L1440,64L1440,320L1360,320C1280,320,1120,320,960,320C800,320,640,320,480,320C320,320,160,320,80,320L0,320Z"></path>
                </svg>
            </div>
        </section>
        <section className={"projects"}>
            <center>
                <div className={"titulos"}>
                    <center>
                        <h4 className={"titulo"}>
                            Seus projetos
                        </h4>
                    </center>
                </div>
            </center>
            <div className={"cards"} area="projects">
                <div className={"container"}>
                    <div className={"row"}>
                        <div className={"col"}>
                            <div className={"card"}>
                                <div className={"row"}>
                                    <div className={"col"}>
                                        <span className={"title"}>
                                            Nome do Projeto
                                        </span>
                                    </div>
                                    <div className={"col"}>
                                        <span className={"title"}>
                                            Data de Entrega
                                        </span>
                                    </div>
                                    <div className={"col"}>
                                        <span className={"title"}>
                                            Nota
                                        </span>
                                    </div>
                                    <div className={"col"}>
                                        <span className={"title"}>
                                            Ações
                                        </span>
                                    </div>
                                    </div>
    {!projetos.projetos.length > 0 ? ( <center> <hr /> <div class="titulos"> <h4 class="desc"> Você não possue nenhum projeto! </h4> </div> </center>) : projetos.projetos.map(data => {
        return (
            <>
                    <div class="row">
                                <div class="col">
                                    <span class="name">
                                        { data.nome }
                                    </span>
                                </div>
                                <div class="col">
                                    <span class="date">
                                       { moment(data.createAt).format("DD/MM/YYYY [ás] HH:MM") }
                                    </span>
                                </div>
                                <div class="col">
                                    <span class="nota">
                                      { data.estrelas }
                                    </span>
                                </div>
                                <div class="col">
                                    <div class="buttons">
                                        <Link class="btn btn-download" to={data.download}>
                                            <i class="mdi mdi-cloud-download"></i>
                                        </Link>
                                        <button class="btn btn-information">
                                            <i class="mdi mdi-information-outline"></i>
                                        </button>
                                    </div>
                                </div>
                            </div>
            </>
        )
        })
    }
                                        
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
            
        </section>
            </>
        )
    } else {
        return (
            <>
            <center>
            <div class="titulos">
                    <h5 class="titulor">ERRO!</h5>
                <p class="desc">Você não está logado! Clique <Link to="/financeiro/login">Aqui</Link> para ser redirecionado</p>
                </div>
            </center>
                </>
        )
    }
}

export default Projetos;