import React from 'react';


function Home() {
    
    return (
        <>
        
<section className="header" area="home" id="home">
            <div className="content">
    <div className="hero" id="hero">
        <div className="textos">
            <h4 className="titulo">
                Olá!
            </h4>
            <p className="desc">
                Meu nome é KoddyDev e sou desenvolvedor Back-End a cerca de um ano e meio, Desenvolvedor Discord a cerca de 3 anos venho desenvolvendo alguns projetos para a comunidade e pretendo tornar um desenvolvedor profissional.
            </p>
        </div>
        <img className="imagem overflow" src="public/img/icons/pixeltrue-web-development-1.svg" alt="Logo 2" />
    </div>
    </div>
</section>
<section className="work">
    <center>
        <div className="titulos">
            <center>
                <h4 className="titulo">
                    Ferramentas
                </h4>
                <p className="desc">
                    Veja todas as coisas que eu uso para fazer meus projetos.
                </p>
            </center>
        </div>
    </center>
    <div className="cards" area="home">
        <div className="container">
            <div className="row">
                <div className="col-md-4">
                    <div className="card discord">
                        <i className="mdi mdi-discord"></i>
                        <hr />
                        <span className="titulo">
                            Integrações
                        </span>
                        <p className="desc">
                            Eu utilizo varias ferramentas para que o seu projeto fique perfeito.
                            <br />
                            <code>Sequelize</code>, <code>Discord.js</code>, <code>Express</code> e <code>ReactJS</code>
                        </p>
                    </div>
                </div>
                
                <div className="col-md-4">
                    <div className="card nodejs">
                        <i className="mdi mdi-nodejs"></i>
                        <hr />
                        <span className="titulo">
                            NodeJS
                        </span>
                        <p className="desc">Uma linguagem de programação bem facil e rapido para o uso.</p>
                    </div>
                </div>
                <div className="col-md-4">
                    <div className="card js">
                        <i className="mdi mdi-language-javascript"></i>
                        <hr />
                        <span className="titulo">
                            JavaScript
                        </span>
                        <p className="desc">Simples e facil, para a criptografia de dados para sua segurança :) </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
        </>
    )
}

export default Home;