import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Image from '../Navbar/Logo.png'

function getDados(setAdmin, setSucess){
    fetch('http://localhost:8080/api/users?token='+localStorage.getItem("TOKEN_KEY")+"&authenticateToken=JG2OI@JWG2131*j)(", {
        method: "GET"
    }).then(res => res.json())
    .then(user => {
        if(user.sucess) {
setSucess(true)
                        setAdmin(user.admin)
        } else {
           setSucess(false)
        }
    });
}

function Header() {
    
    let [admin, setAdmin] = useState(false),
    [sucess, setSucess ] = useState(false);

    useEffect(() => {
        getDados(setAdmin, setSucess)
    }, [])
if(sucess)
    return (
        <>
        <section className={"header"} area="client">
        <nav className={"navbar navbar-expand-lg"}>
    <div className={"brand"}>
        <div className={"logo"}>
            <img src={Image} alt="Logo" to="/" />
        </div>
    </div>
    <div className={"switcher"}>
        <button className={"navbar-toggler"} type="Link" data-toggle="collapse" data-target="#Links" aria-controls="Links" aria-expanded="false" aria-label="Switcher Responsivo">
            <i className={"mdi mdi-view-day"}></i>
        </button>
    </div>
    <div className="container">
        <div className="collapse navbar-collapse" id="buttons">
            <ul className="navbar-nav buttons">
                <li className="nav-item">
                    <Link className="nav-link" to="/financeiro">
                        Início
                    </Link>
                </li>
                <li className="nav-item">
                    <Link className="nav-link" to="/financeiro/projetos">
                        Meus Projetos
                    </Link>
                </li>
                <li className="nav-item">
                    <Link className="nav-link" to="/financeiro/account">
                        Minha Conta
                    </Link>
                </li>
                <li className="nav-item">
                    <Link className="nav-link" to="/financeiro/ranks">
                        Ranks
                </Link>
                </li>
                {admin &&
                <li className="nav-item">
                <Link className="nav-link" to="/admin">
                <i className={"mdi mdi-cogs"}></i>
            </Link>
            </li>
            }
                <li className="nav-item">
                    <Link to="/financeiro/logout" className="nav-link cliente" >
                        Logout
                    </Link>
                </li>
            </ul>
        </div>
    </div>
</nav></section>
        </>
    );

    return (
        <>
        <center>
        <div class="titulos">
                <h5 class="titulor">ERRO!</h5>
            <p class="desc">Você não está logado! Clique <Link to="/financeiro/login">Aqui</Link> para ser redirecionado</p>
            </div>
        </center>
            </>
    )
}

export default Header;