import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import Footer from '../Footer/Footer';

import HeaderC from './Navbar';

function getDados(setUser, setSucess){
    fetch('http://localhost:8080/api/users?token='+localStorage.getItem("TOKEN_KEY")+"&authenticateToken=JG2OI@JWG2131*j)(", {
        method: "GET"
    }).then(res => res.json())
    .then(user => {
        if(user.sucess) {
            setUser(user)
            setSucess(true)    
        }
    }).catch(err => {
        if(err === "TypeError: Failed to fetch")  {
                return (
                  <>
                  <center>
                  <div class="titulos">
                          <h5 class="titulor">ERRO!</h5>
                      <p class="desc">Estamos com problemas entre nosso servidor e a nossa API, nosso técnico já foi alertado e estaremos trabalhando para resolver o mais rapido possivel. </p>
                      </div>
                  </center>
                      </>
                )
                }
});
}

function Home() {
    let [usuario, setUser] = useState({})
    let [sucess, setSucess] = useState(false)
    useEffect(() => {
        getDados(setUser, setSucess)
    }, [])
    
if(sucess) {
    return (
        <>
        <section className="header" area="client">
        <HeaderC />
    <center>
        <h3>
            Seja bem vindo a área de cliente
        </h3>
    </center>
    <div className="divisor mt-2">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
            <path fill="var(--dark)" fill-opacity="1" d="M0,64L80,80C160,96,320,128,480,117.3C640,107,800,53,960,37.3C1120,21,1280,43,1360,53.3L1440,64L1440,320L1360,320C1280,320,1120,320,960,320C800,320,640,320,480,320C320,320,160,320,80,320L0,320Z"></path>
        </svg>
    </div>
</section>
<section className="client">
    <div className="cards" area="client-info">
        <div className="container">
            <div className="row">
                <div className="col-md-4">
                    <div className="card ranking">
                        <div className="row">
                            <div className="col">
                                <div className="info">
                                    <span className="titulo">
                                        Seu Ranking
                                    </span>
                                    <h5>{ usuario.rank }</h5>
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="icon">
                                    <i className="mdi mdi-information-outline"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div className="card money">
                        <div className="row">
                            <div className="col">
                                <div className="info">
                                    <span className="titulo">
                                        Dinheiro Gasto
                                    </span>
                                    <h5>{ usuario.gasto }</h5>
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="icon">
                                    <i className="mdi mdi-cash"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-md-4">
                    <div className="card projects">
                        <div className="row">
                            <div className="col">
                                <div className="info">
                                    <span className="titulo">
                                        Projetos
                                    </span>
                                    <h5>{ usuario.projetos }</h5>
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="icon">
                                    <i className="mdi mdi-view-dashboard-outline"></i>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
<Footer />
        </>
    )
} else {
    return (
        <>
        <center>
        <div class="titulos">
                <h5 class="titulor">ERRO!</h5>
            <p class="desc">Você não está logado! Clique <Link to="/financeiro/login">Aqui</Link> para ser redirecionado</p>
            </div>
        </center>
            </>
    )
}
}

export default Home;
