import React from 'react'
import { Switch, Route, Redirect, useLocation } from 'react-router-dom';
import HomeC from '../components/ClientArea/Home';
import Projetos from '../components/ClientArea/Projetos'
import Login from '../components/ClientArea/Auth/Container'
import Ranks from '../components/ClientArea/Ranks'
import Account from '../components/ClientArea/Account';
import Logout from '../components/ClientArea/Auth/Logout';

function PrincipalRouter () {
        let location = useLocation();
        if(location.pathname.startsWith("/financeiro")) {
        if(!localStorage.getItem("TOKEN_KEY")) {
                return (
                  <>
                  
            <Redirect to={'/financeiro/login'} /> 
            <Login />
            </>
            )
        } else {

                fetch("http://localhost:8080/api/users/?token="+localStorage.getItem("TOKEN_KEY")+"&authenticateToken=JG2OI@JWG2131*j)(")
                .then((req) => {
            
                  if(!req.body.loggedin || req.status === 400) return (
                          <>
                          <Redirect to={'/financeiro/login'} /> 
            <Login />
                          </>
                  );
              }).catch(err => {
                      if(err = "TypeError: Failed to fetch")  {
                              return (
                                <>
                                <center>
                                <div class="titulos">
                                        <h5 class="titulor">ERRO!</h5>
                                    <p class="desc">Estamos com problemas entre nosso servidor e a nossa API, nosso técnico já foi alertado e estaremos trabalhando para resolver o mais rapido possivel. </p>
                                    </div>
                                </center>
                                    </>
                              )
                              }
              })
        
    return (
        <>
                <Switch>
                    <Route exact path={'/financeiro'}>
                            <HomeC />
                    </Route>
                    <Route exact path={'/financeiro/projetos'}>
                            <Projetos />
                    </Route>
                    <Route exact path={'/financeiro/ranks'}>
                            <Ranks />
                    </Route>
                    <Route exact path={'/financeiro/account'}>
                            <Account />
                    </Route>
                    <Route exact path={'/financeiro/login'}>
                            <Login />
                    </Route>
                    <Route exact path={'/financeiro/logout'}>
                            <Logout />
                    </Route>
                    
                    <Route path={'*'} >
                            <Redirect to="/financeiro/login" />
                    </Route>
                </Switch>
        </>
    );
}
} else return null;
}

export default PrincipalRouter;

